:root{
  /* Primay Colors */
  --bright-blue: hsl(220, 98%, 61%);
  --check-background: linear-gradient(hsl(192, 100%, 67%), hsl(280, 87%, 65%));

  --background-body: hsl(0, 0%, 98%);
  --backgrounds: hsl(0, 0%, 98%);
  --background-image-mb: url(./images/bg-mobile-light.jpg) no-repeat;
  --background-image-desk: url(./images/bg-desktop-light.jpg) no-repeat;
  --box-shadow: 0px 1px 15px rgb(160, 160, 160);
  --letter-color: hsl(235, 21%, 11%);
  --hover-clear: hsl(235, 19%, 35%);
  --hover-status: hsl(235, 21%, 11%);
  --border-color: hsl(233, 11%, 84%);
  --span-and-status: hsl(236, 9%, 61%);
  --info-text: hsl(236, 9%, 61%);


  /* Typography */
  --font-josefin-sans: 'Josefin Sans', sans-serif;
}

[data-theme='dark']{
  --background-body: hsl(235, 21%, 11%);
  --backgrounds: hsl(235, 24%, 19%);
  --background-image-mb: url(./images/bg-mobile-dark.jpg) no-repeat;
  --background-image-desk: url(./images/bg-desktop-dark.jpg) no-repeat;
  --box-shadow: 0 5px 50px hsl(0, 0%, 2%);
  --letter-color: hsl(233, 11%, 84%);
  --hover-clear: hsl(236, 33%, 92%);
  --hover-status: hsl(0, 0%, 98%);
  --border-color: hsl(233, 14%, 35%);
  --info-text: hsl(233, 14%, 35%);
}

html, body, #root, .section-light, .section{
  overflow-x: hidden;
  width: 100%;
  height: 100%;
}

body{
  font-size: 18px;
  font-family: var(--font-josefin-sans);
}

.section{
  background: var(--background-image-mb);
  background-size: 100% 250px;
  background-color: var(--background-body)
}

.article{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 95%;
  max-width: 600px;
  margin: 0 auto;
}

.container{
  display: flex;
  width: inherit;
  justify-content: space-between;
  align-items: center;
  padding-top: .6em;
}

.container h1{
  font-size: 2rem;
  letter-spacing: 20px;
  color: #fff;
}

.theme-btn{
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  cursor: pointer;
} 

.todo-form{
  width: inherit;
  margin-top: 1em;
  height: 60px;
}

.todo-label{
  display: grid;
  grid-template-columns: 25px 145px auto;
  align-items: center;
  height: inherit;
  gap: 15px;
  padding-right: .5em;
  padding-left: .5em;
  border-radius: 5px;
  background-color: var(--backgrounds);
}

.form-circle{
  margin-left: 10px;
  width: 25px;
  height: 25px;
  border: 1px solid var(--border-color);
  border-radius: 50%;
  box-sizing: border-box;  

}

.form-span{
  font-size: 1rem;
  min-width: 15ch;
  margin-top: .2em;
  margin-left: 1em;
  color: var(--span-and-status);
}

.form-input-text{
  font-size: 1.3rem;
  font-family: var(--font-josefin-sans);
  border: none;
  min-width: 70px;
  background-color: var(--backgrounds);
  color: var(--bright-blue);
}

.form-input-text:focus{
  border: none;
  outline: none;
}

.todo-container{
  position: relative;
  width: inherit;
  margin-top: 1em;
  border-radius: .3em;
  background-color: var(--backgrounds);
  box-shadow: var(--box-shadow);
  color: var(--letter-color);
}

.todo-list{
  list-style: none;
  padding: 0;
  margin: 0;
}

.todo-list-item{
  display: grid;
  grid-template-columns: 20px auto 30px;
  align-items: center;
  padding: 1em;
  border-bottom: 1px solid var(--border-color);
}

.todo-list-label{
  font-size: 1rem;
  margin-left: 2em;
}

.todo-list-status{
  display: flex;
  justify-content: space-between;
  padding: .5em 1em;
  list-style: none;
}

.todo-list-status li{
  font-size: 1rem;
  color: var(--span-and-status);
}

.todo-checkbox{
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid var(--border-color);
  outline: none;
}

.todo-checkbox:checked:after{
  content: '';
  display: block;
  width: 25px;
  height: 25px;
  background: url(images/icon-check.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.todo-checkbox:checked{
  background: var(--check-background);
}

.todo-checkbox, .todo-task, .btn-delete{
  cursor: pointer;
}

.btn-delete{
  border: none;
  outline: none;
  background: none;
}

.clear-btn{
  border: none;
  background: none;
  cursor: pointer;    
  font-family: var(--font-josefin-sans);
  font-size: 1rem;
  padding: 0;
  color: var(--span-and-status);
}


.todo-status{
  position: absolute;
  left: 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 60px;
  width: 100%;
  margin-top: 70px;
  padding: 0;
  box-shadow: var(--box-shadow);
  font-size: 1rem;
  font-weight: 600;
  list-style: none;
  border-radius: .2em;
  background-color: var(--backgrounds);
}

.radio-btn{
  appearance: none;
}

.label-text{
  cursor: pointer;
}

.radio-btn:checked + .label-text{
  color: var(--bright-blue);
}


.info-text{
  margin-top: 150px;
  color: var(--info-text);
}

@media screen and (min-width: 768px) {
  .section{
      background: var(--background-image-desk);
      background-size: 100% 260px;
      background-color: var(--background-body)
  }

  .todo-list-status{
      display: grid;
      grid-template-columns: 100px 225px 123px;
      justify-content: space-between;
      padding-left: 1.5em;
      padding-right: 1.5em;
  }

  .todo-status{
      all: unset;
      display: flex;
      gap: 1em;
  }

  .info-text{
  margin-top: 55px;
  }
}

@media screen and (hover:hover) {
  .todo-checkbox:not(:checked):hover{
  background: 
  linear-gradient(var(--backgrounds) 0 0) padding-box,
  linear-gradient(140deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%)) border-box;
  border: 1px solid transparent;
  }

  .btn-delete{
      display: none;
  }

  .todo-list-item:hover > .btn-delete{
      display: block;
  }

  .clear-btn:hover{
  color: var(--hover)
  }

  .label-text:hover{
  color: var(--hover-status);
  }
}

